import { dbQuery } from '../database';

/**
 * Capitalizes first letter in string
 * 
 * @param {String} string 
 * @returns {String} with first letter capatalized
 */
function capitalizeFirstLetter(str) {
  return str.charAt(0).toUpperCase() + str.slice(1);
}

/**
 * Fetches data from database for Icelandic name declesion (fallbeygingar)
 *
 * @param {String} name - Name string
 * @returns {Array<String>}
 */
export async function getDeclensionByName(str) {
  try {
    const { rows } = await dbQuery(
      'SELECT * FROM bin WHERE uppflettiord = $1',
      [capitalizeFirstLetter(str)]
    );
    
    const parsedData = parseDeclesionNameResponse(rows);
    console.log(parsedData);
    
    return parsedData;
  } catch (error) {
    return new Error(error);
  }
}

/**
 * Parses Database query response from table bin
 *
 * @param {Array} row
 * @returns {Array} only containing declesions
 * @example parseDeclesionNameResponse(rows) => ['Snær','Snæ', 'Snæ', 'Snæs']
 */
function parseDeclesionNameResponse(row) {
  const arr = [];
  
  for (const item of row) {
    const str = item.greiningarstrengur;

    if(/NF/.test(str)) {
      arr[0] = item.beygingarmynd;
    } else if(/ÞF/.test(str)) {
      arr[1] = item.beygingarmynd;
    } else if(/ÞGF/.test(str)) {
      arr[2] = item.beygingarmynd;
    } else if(/EF/.test(str)) {
      arr[3] = item.beygingarmynd;
    }
  }

  return arr;
}


